// ChatRoomList.tsx

import React, { useState, useEffect } from 'react';
import { getChatRooms } from '@/api/chatService/chat';
import type { ChatRoom } from '@/types/chatService/chat';
import { Search, Plus } from 'lucide-react';

interface ChatRoomListProps {
  currentUserId: string;
  onSelectRoom: (roomId: string, roomName: string) => void;
  onCreateRoom?: () => void;
}

export default function ChatRoomList({ currentUserId, onSelectRoom, onCreateRoom }: ChatRoomListProps) {
  const [rooms, setRooms] = useState<ChatRoom[]>([]);
  const [loading, setLoading] = useState(false);
  const [searchQuery, setSearchQuery] = useState('');

  useEffect(() => {
    if (currentUserId) {
      loadRooms();
    }
  }, [currentUserId]);

  const loadRooms = async () => {
    setLoading(true);
    try {
      const data = await getChatRooms(currentUserId); // currentUserId 전달
      setRooms(data);
    } catch (err: any) {
      console.error('채팅방 목록 로드 실패:', err);
    } finally {
      setLoading(false);
    }
  };

  // 수정된 formatTime 함수 (문자열을 반환하도록 보장)
  const formatTime = (dateString?: string): string => {
    if (!dateString) return '알 수 없음';

    const now = new Date();
    const messageTime = new Date(dateString);
    const diffInMs = now.getTime() - messageTime.getTime();
    const diffInMinutes = Math.floor(diffInMs / (1000 * 60));
    const diffInHours = Math.floor(diffInMs / (1000 * 60 * 60));
    const diffInDays = Math.floor(diffInMs / (1000 * 60 * 60 * 24));

    if (diffInMinutes < 1) return '방금 전';
    if (diffInMinutes < 60) return `${diffInMinutes}분 전`;
    if (diffInHours < 24) return `${diffInHours}시간 전`;
    if (diffInDays < 7) return `${diffInDays}일 전`;

    // 7일 이상이면 날짜 포맷 (예: 12.25 또는 2025.01.01)
    const isThisYear = now.getFullYear() === messageTime.getFullYear();
    if (isThisYear) {
      return `${messageTime.getMonth() + 1}.${messageTime.getDate()}`;
    }
    return `${messageTime.getFullYear().toString().slice(2)}.${(messageTime.getMonth() + 1).toString().padStart(2, '0')}.${messageTime.getDate().toString().padStart(2, '0')}`;
  };

  const filteredRooms = rooms.filter(room =>
      room.roomName?.toLowerCase().includes(searchQuery.toLowerCase()) ||
      room.lastMessageContent?.toLowerCase().includes(searchQuery.toLowerCase())
  );

  return (
      <div className="flex flex-col h-full bg-white">
        {/* 헤더 및 검색 */}
        <div className="p-4 border-b border-gray-200">
          <h2 className="text-2xl font-bold text-gray-800 mb-4">채팅</h2>
          <div className="flex space-x-2">
            <div className="relative flex-1">
              <Search className="absolute left-3 top-1/2 -translate-y-1/2 w-4 h-4 text-gray-400" />
              <input
                  type="text"
                  placeholder="채팅 검색"
                  value={searchQuery}
                  onChange={(e) => setSearchQuery(e.target.value)}
                  className="w-full pl-10 pr-4 py-2 border border-gray-300 rounded-xl focus:outline-none focus:ring-2 focus:ring-blue-500"
              />
            </div>
            <button
                onClick={onCreateRoom}
                className="p-2 bg-blue-500 text-white rounded-xl hover:bg-blue-600 transition"
                title="새 채팅 시작"
            >
              <Plus className="w-5 h-5" />
            </button>
          </div>
        </div>

        {/* 채팅방 목록 */}
        <div className="flex-1 overflow-y-auto">
          {loading ? (
              <div className="p-4 text-center text-gray-500">채팅방 목록을 불러오는 중...</div>
          ) : filteredRooms.length === 0 ? (
              <div className="p-4 text-center text-gray-500">채팅방이 없습니다.</div>
          ) : (
              <div className="divide-y divide-gray-100">
                {filteredRooms.map((room) => (
                    <div
                        key={room.roomId}
                        onClick={() => onSelectRoom(room.roomId, room.roomName || room.participantIds.filter(id => id !== currentUserId).join(', '))}
                        className="flex items-center p-4 cursor-pointer hover:bg-gray-50 transition-colors group"
                    >
                      {/* 프로필 이미지 */}
                      <div className="relative mr-4 flex-shrink-0">
                        <div className="w-12 h-12 rounded-full bg-gradient-to-br from-indigo-500 to-purple-500 flex items-center justify-center shadow-sm group-hover:shadow-md transition-shadow">
                    <span className="text-white font-bold text-lg">
                      {room.roomName ? room.roomName.charAt(0) : '?'}
                    </span>
                        </div>
                      </div>

                      {/* 채팅방 정보 */}
                      <div className="flex-1 min-w-0">
                        <div className="flex items-center justify-between mb-1">
                          <h3 className="font-semibold text-gray-900 truncate group-hover:text-blue-600 transition-colors">
                            {room.roomName || '이름 없는 방'}
                          </h3>
                          <span className="text-xs text-gray-400 ml-2 flex-shrink-0">
                      {formatTime(room.lastMessageTime)}
                    </span>
                        </div>
                        <div className="flex items-center justify-between">
                          <p className="text-sm text-gray-500 truncate group-hover:text-gray-700">
                            {room.lastMessageContent || '새로운 대화가 시작되었습니다.'}
                          </p>
                        </div>
                      </div>
                    </div>
                ))}
              </div>
          )}
        </div>
      </div>
  );
}