// src/hooks/useWebSocket.ts
import { useEffect, useRef, useState, useCallback } from 'react';
import { Client } from '@stomp/stompjs';
import SockJS from 'sockjs-client';

// SockJS는 http/https 로 시작하는 URL만 허용한다 → ws:// → http:// 로 바꿔줘야 함
function getSockJSUrl(originalUrl: string): string {
  // ws://localhost:8084/ws-chat   → http://localhost:8084/ws-chat
  // wss://api.example.com/chat  → https://api.example.com/chat
  return originalUrl.replace(/^ws(s)?:\/\//, (matched, s) => (s ? 'https://' : 'http://'));
}

interface UseWebSocketProps {
  /** http:// 또는 https:// 로 시작해도 되고, ws:// 로 시작해도 됩니다. */
  url: string;
  roomId?: string;
  onMessage: (message: any) => void;
}

export function useWebSocket({ url, roomId, onMessage }: UseWebSocketProps) {
  const clientRef = useRef<Client | null>(null);
  const [isConnected, setIsConnected] = useState(false);

  const connect = useCallback(() => {
    const sockUrl = getSockJSUrl(url); // 여기서 자동 변환

    const client = new Client({
      webSocketFactory: () => new SockJS(sockUrl), // 반드시 http/https
      reconnectDelay: 5000,
      heartbeatIncoming: 4000,
      heartbeatOutgoing: 4000,

      // 디버그는 개발할 때만 켜세요 (프로덕션에서는 끄는 게 좋음)
      debug: (str) => {
        if (process.env.NODE_ENV === 'development') {
          console.log('STOMP →', str);
        }
      },

      onConnect: () => {
        console.log('WebSocket 연결 성공');
        setIsConnected(true);

        if (roomId) {
          client.subscribe(`/topic/chat/${roomId}`, (msg) => {
            try {
              const data = JSON.parse(msg.body);
              onMessage(data);
            } catch (e) {
              console.error('메시지 파싱 실패', e);
            }
          });
          console.log(`구독 완료: /topic/chat.room.${roomId}`);
        }
      },

      onStompError: (frame) => {
        console.error('STOMP 오류', frame.headers['message'], frame.body);
        setIsConnected(false);
      },

      onWebSocketError: (error) => {
        console.error('WebSocket 오류', error);
      },

      onWebSocketClose: () => {
        console.log('WebSocket 연결 종료');
        setIsConnected(false);
      },
    });

    client.activate();
    clientRef.current = client;
  }, [url, roomId, onMessage]);

  useEffect(() => {
    connect();

    return () => {
      if (clientRef.current) {
        clientRef.current.deactivate();
        clientRef.current = null;
      }
    };
  }, [connect]);

  const sendMessage = useCallback(
      (payload: any) => {
        if (clientRef.current?.connected) {
          clientRef.current.publish({
            destination: '/app/chat.message', // 백엔드 @MessageMapping 경로와 정확히 일치해야 함
            body: JSON.stringify(payload),
          });
        } else {
          console.warn('WebSocket 연결 안됨 → 메시지 전송 실패', payload);
        }
      },
      []
  );

  return { isConnected, sendMessage };
}